@import '../../../assets/styles/_variables.scss';

.slide{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .left{
        width: 50%;
        h3{
            margin: 15px 0;
            font-size: 22px;
        }
        p{
            margin: 20px 0;
            font-size: 18px;
            width: 70%;
        }
    }
    .right{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50%;
        img{
            width: 70%;
        }
    }
}