@import "../../common/assets/styles/_variables";
/*-------------     HEAD            ------------------*/
.head{
    background-size: cover;
    background-repeat: no-repeat;
    // background-attachment: fixed;
    background-position: center;
    color: white;
    padding-top: 20px;

    a{
        color: white;
    }

    .menu-logo{
        width: 100%;
    }
    .about{
        height: 600px;
    }
    
    .container{
        flex-direction: column;
    }

}

.logo{
    img{
        width: 200px;
    }

}

.menu{
    margin-top: 1em;
    font-weight: 600;
    font-size: 1.1vw;
    -webkit-transition: .25s;
    transition: .25s;
    li{
        margin-left: 2em;
        -webkit-transition: .25s;
        transition: .25s;
        border-bottom: 3px solid transparent;
        &:hover{
            border-bottom: 3px solid $f_color;
            -webkit-transition: .25s;
    transition: .25s;
        }
    }

}

.about{
    justify-content: center;
    h1{
        font-size: 2.5vw;
    }

    p{
        width: 40%;
        font-size: 1.2vw;
        text-align: center;
        margin-bottom: 1em;
    }
}