@import "../../common/assets/styles/_variables";

.about-com{
    margin-top: 10px;
    flex-direction: column;
    justify-content: center;

    .about-text{
        width: 80%;
        text-align: center;
        justify-content: center;
        margin-bottom: 40px;
        p{
            font-size: 20px;
            font-weight: 400;
            line-height: 1.4em;
        }
    }

    .about-benefits{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        align-items: center;
    }

    .about-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 50px;

    }
}