@import "../../assets/styles/_variables";


.btn{
    background-color: $f_color;
    color: #fff;
    border: 2px solid $f_color; 
    -webkit-transition: ease background-color 1s;
    transition: ease color 0.5s, background-color 1s;
    cursor: pointer;   
    
    padding: 0;   
}

.btnTextShadow{
    text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
}

.btnLetterSpacing{
    letter-spacing: .16em;
}

.btnFontWeight{
    font-weight: 700;
}

.border_button{
    background-color: rgba($f_rgb_color, 0);
    border: 2px solid $f_color;    
    &:hover{
        background-color: rgba($f_rgb_color, 1);    
        -webkit-transition: ease background-color 1s;
        transition: ease background-color 1s;
    }}
.hoverBlack{
    &:hover{
        color: #000;
        transition-property: color;
        transition-duration: 0.5s;
    }    
}

.default_button{
    &:hover{
        background-color: rgba($f_rgb_color, 0);
        -webkit-transition: ease background-color 1s;
        transition: ease background-color 1s;  
    }
}
