@import '../../../assets/styles/_variables.scss';

.stocks-layer{
 display: flex;
 justify-content: center;
 align-items: center;
 background-color: $f_stock_color;
 font-size: 20px;
 font-weight: 700;
 color: #fff;   
 border-radius: 20px;
 padding: 5px 10px;
 height: 32px;
 width: 40%;
 span{
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
   img{
      width: 28px;
      margin-left: 8px;
   }
 }
}