@import "../assets/styles/_variables";

footer{
    background-color: black;
    color: #fff;
    padding: 25px 0;   
    width: 100%;
    align-self: flex-end;
    .top{
        display: flex;
        justify-content: space-between;
        flex-direction: row;  
    }
    .bottom{
        display: flex;
        border-bottom: 1px solid #F5F5F5;
        padding-bottom: 10px;
        flex-direction: row;
        margin-top: 20px;
    }
    .socials{
        display: flex;
        flex-direction: row;
        justify-content: end;
        width: 100%;
        a i{
            color: #fff;
            font-size: 26px;
            margin-left: 10px;
            color: $f_color;
            &:hover{
                color: #fff;
            }
        }
        
    }
    .left .logo{
        width: 200px;
        margin-bottom: 50px;
    }
    .left h4{
        text-transform: uppercase;
        margin-bottom: 10px;
        font-size: 20px;
    }
    .left p{
        margin-bottom: 10px;
        font-size: 18px;
    }
    .left p i{
        color: #fff;
        font-size: 20px;
        margin-right: 10px;
        color: $f_color;
    }

    .right{
        margin-top: 15px;
        display: flex;
        flex-direction: column;

        form input, form textarea{
            display: flex;
            width: 100%;
            margin-bottom: 15px;
            background-color: #000;
            border: 2px solid $f_color;
            border-radius: 10px;
            padding: 5px 10px;
            color: #fff;
            outline: none;
        }
    }
}