@import '../../assets/styles/_variables.scss';

.clients-block{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    &:before{
        background-color: rgba(0, 0, 0, 0.47);
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 600px;
        z-index: 1;
      }
    .container{
        z-index: 2;
        .heading .h1{
            border-bottom: 3px solid $f_color;
        }
        .clients-btn{
            width: 100%;
            text-align: center;
            margin-top: 20px;
        }

    }
}