@import '../../common/assets/styles/_variables';

.category-card{
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap:wrap;
  justify-content:space-around;
  margin-bottom: 20px;
  .category{
    margin-top: 10px;
    padding: 20px 5px;  
    display: flex;
    flex-direction: column;
    width: 200px;
    align-items: center;
    justify-content: center;
    border: 2px solid rgba($color: #000000, $alpha: 0.3);
    border-radius: 15px;
    font-weight: 700;
    transition: ease .5;
    text-align: center;    
    a{
      text-decoration: none;
      color:  #000;
      &:hover{
        color: $f_color;
      }
    }
    &:hover{
      border: 2px solid rgba($color: #FCA522, $alpha: 1);
      color: $f_color;
    }

    img{
      width: 100%;
      height: 60%;
      margin-bottom: 30px;
    }

  }
}